.Slider {
    width: 100vw;
    height: 100vh;
    margin: 0;
    position: absolute;
    overflow-x: visible;
    -webkit-transform: translateX(0);
        -ms-transform: translateX(0);
            transform: translateX(0);
    -webkit-transition: none;
    -o-transition: none;
    transition: none;
}


.Slide {
    position: absolute;
    min-height: 100vh;
    overflow-y: scroll;
    width: 100%;
    padding: 100px 20px 20px;
    -webkit-transform: translateX(0);
        -ms-transform: translateX(0);
            transform: translateX(0);
}


